.contents{
    padding-top: 0;
    padding-bottom: 80px;
    @media screen and (min-width: 768px) {
        padding-top: 0;
        padding-bottom: 120px;
    }
}

.searchgourmetWrap{
    margin-top: 10px;
    @media screen and (min-width: 768px) {
        margin-top: 80px;
    }

    .pageTitle{
        display: flex;
        align-items: center;
        gap: 20px;
        font-size: 25px;
        font-weight: 700;
        @media screen and (min-width: 768px) {
            font-size: 50px;
        }
    }

    .filterWrap{
        display: flex;
        flex-wrap: wrap;
        margin-top: 15px;
        @media screen and (min-width: 768px) {
            flex-wrap: nowrap;
            justify-content: space-between;
            margin-top: 50px;
        }

        .filter-1{
            display: flex;
            align-items: center;
            justify-content: flex-start;
            gap: 10px;
            flex-wrap: nowrap;
            overflow-x: auto;
            white-space: nowrap;

            button{
                display: flex;
                align-items: center;
                justify-content: center;
                padding: 0 23px;
                height: 40px;
                color: #A0A0A0;
                font-size: 14px;
                font-weight: 700;
                line-height: 1;
                background-color: var(--white);
                border: 1px solid #D5D5D5;
                border-radius: 8px;;

                &:hover, &.active{
                    color: var(--white);
                    background-color: var(--orange);
                }
            }
        }

        .filter-2{
            margin-top: 20px;
            @media screen and (min-width: 768px) {
                margin-top: 0;
            }
        }
    }
}

.searchgourmetList {
    margin-top: 30px;
}
